@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);

.card{
    background: rgba(255, 255, 255, 0.411);
    width: 250px;
    height: 400px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    transition: transform 0.2s ease-in;
    letter-spacing: 2px;
    margin: 20px 10px;
}

.card:hover{
    transform: scale(1.01);
}

.card h3{
    padding: 7px  15px;
    border-radius: 26px ;
    background-color:  rgba(66, 66, 66, 0.137);
    font-size: 16px;
    background-color:rgba(255, 255, 255, 0.747);
    color: rgba(88, 88, 88, 0.795);
    text-transform: capitalize;
}

.infoContainer{
    width: 150px;
}

.info{
    background-color: rgba(255, 255, 255, 0.747);
    padding: 7px;
    border-radius: 26px 0 0 26px;
    display: inline-block;
    width: 50%;
    color: rgba(88, 88, 88, 0.795);
}
.value{
    background-color: rgba(66, 66, 66, 0.137);
    display: inline-block;
    padding: 7px;
    border-radius: 0px 26px 26px 0px;
    width: 50%;
    color: white;
}


.pokeImgContainer{
    background-color: rgba(255, 255, 255, 0.39);
    border-radius: 50%;
    width: 150px;
    height: 150px;
}

.typeContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}

.typeIcon{
    max-width: 30px;
    margin: 4px;
}

.pokeImg{
    max-width: 150px;
}


.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:center;
    
}



h1{
    font-weight: 400;
    font-size: 40px;
    margin-top: 10px;
    text-align: center;
    color: rgba(255, 255, 255, 0.753);
}

#logo{
    width:400px; 
    margin-top:30px;
}



@media(max-width:400px){
    #logo{
        width:300px; 
        margin-top:20px;
    }   
}



.searchDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 600px;
    margin: 0 auto;
}


#searchbar{
    height: 30px;
    margin: 20px auto;
    width: 80%;
    border-radius: 2px 0 0 2px;
    border:none;
    padding-left: 10px;
}
.searchDiv select{
    height: 30px;
    width: 100px;
    border: none;
}

@media(max-width:820px){
    .searchDiv{
        width:500px; 
      
    }   
}
@media(max-width:540px){
    .searchDiv{
        width:230px; 
        flex-direction: column;
    }   
    #searchbar{
        margin: 0;
        margin-top: 10px;
        width: 100%;
    }
    .searchDiv select{
        width: 100%;
    }
}


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
}
body{
    background: rgb(54,59,129);
    background: linear-gradient(90deg, rgba(54,59,129,1) 0%, rgba(71,114,184,1) 22%, rgba(82,150,220,1) 33%, rgba(88,170,240,1) 40%, rgba(91,179,249,1) 45%, rgba(93,185,255,1) 49%, rgba(92,181,251,1) 54%, rgba(90,175,245,1) 58%, rgba(85,157,227,1) 63%, rgba(73,119,189,1) 76%, rgba(54,59,129,1) 100%);
}
