
.card{
    background: rgba(255, 255, 255, 0.411);
    width: 250px;
    height: 400px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    transition: transform 0.2s ease-in;
    letter-spacing: 2px;
    margin: 20px 10px;
}

.card:hover{
    transform: scale(1.01);
}

.card h3{
    padding: 7px  15px;
    border-radius: 26px ;
    background-color:  rgba(66, 66, 66, 0.137);
    font-size: 16px;
    background-color:rgba(255, 255, 255, 0.747);
    color: rgba(88, 88, 88, 0.795);
    text-transform: capitalize;
}

.infoContainer{
    width: 150px;
}

.info{
    background-color: rgba(255, 255, 255, 0.747);
    padding: 7px;
    border-radius: 26px 0 0 26px;
    display: inline-block;
    width: 50%;
    color: rgba(88, 88, 88, 0.795);
}
.value{
    background-color: rgba(66, 66, 66, 0.137);
    display: inline-block;
    padding: 7px;
    border-radius: 0px 26px 26px 0px;
    width: 50%;
    color: white;
}


.pokeImgContainer{
    background-color: rgba(255, 255, 255, 0.39);
    border-radius: 50%;
    width: 150px;
    height: 150px;
}

.typeContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}

.typeIcon{
    max-width: 30px;
    margin: 4px;
}

.pokeImg{
    max-width: 150px;
}

