.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:center;
    
}



h1{
    font-weight: 400;
    font-size: 40px;
    margin-top: 10px;
    text-align: center;
    color: rgba(255, 255, 255, 0.753);
}

#logo{
    width:400px; 
    margin-top:30px;
}



@media(max-width:400px){
    #logo{
        width:300px; 
        margin-top:20px;
    }   
}



.searchDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 600px;
    margin: 0 auto;
}


#searchbar{
    height: 30px;
    margin: 20px auto;
    width: 80%;
    border-radius: 2px 0 0 2px;
    border:none;
    padding-left: 10px;
}
.searchDiv select{
    height: 30px;
    width: 100px;
    border: none;
}

@media(max-width:820px){
    .searchDiv{
        width:500px; 
      
    }   
}
@media(max-width:540px){
    .searchDiv{
        width:230px; 
        flex-direction: column;
    }   
    #searchbar{
        margin: 0;
        margin-top: 10px;
        width: 100%;
    }
    .searchDiv select{
        width: 100%;
    }
}
